import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManillasComponent } from './manillas.component';
import { AdminManillasEnviadasResolver, AdminManillasEstadosResolver, AdminManillasListResolver, AdminManillasProduccionResolver, AdminManillasSolicitudesResolver, AdminTypesResolver } from '../admin.resolver';
import { ManillasListComponent } from './list/list.component';
import { ProductionListComponent } from './production/production.component';
import { SolicitudesListComponent } from './solicitudes/solicitudes.component';
import { EnviadasListComponent } from './enviadas/enviadas.component';


const routes: Routes = [
  {
    path: '',
    component: ManillasComponent,
    children:[
      {
        path: 'list',
        component: ManillasListComponent,
        resolve:{
          estados: AdminManillasEstadosResolver,
          types: AdminTypesResolver,
          manillas: AdminManillasListResolver
        },
        data: {
          title: "Pulseras",
          breadcrumb: "Lista"
        },
      },
      {
        path: 'produccion',
        component: ProductionListComponent,
        resolve:{
          manillas: AdminManillasProduccionResolver
        },
        data: {
          title: "Pulseras",
          breadcrumb: "Produccion"
        },
      },
      {
        path: 'solicitudes',
        component: SolicitudesListComponent,
        resolve:{
          manillas: AdminManillasSolicitudesResolver
        },
        data: {
          title: "Pulseras",
          breadcrumb: "Solicitudes"
        },
      },
      {
        path: 'enviadas',
        component: EnviadasListComponent,
        resolve:{
          manillas: AdminManillasEnviadasResolver
        },
        data: {
          title: "Pulseras",
          breadcrumb: "Enviadas"
        },
      }
    ],
    data: {
      title: "Pulseras",
      breadcrumb: "Pulseras"
    },
    // resolve: {
    //   manillas: AdminManillasResolver
    // }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ManillasRoutingModule { }
