import { ViewportScroller } from '@angular/common';
import { ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Subject, takeUntil } from 'rxjs';
import { UserService } from 'src/app/core/user/user.service';
import { AdminService } from '../../admin.service';
import Swal from 'sweetalert2';
import { environment } from 'src/environments/environment';
import { VerManillaAdminComponent } from '../modal/ver-manilla/ver-manilla.component';

@Component({
  selector: 'app-production-list',
  templateUrl: './production.component.html',
  styleUrls: ['../manillas.component.scss']
})
export class ProductionListComponent implements OnInit, OnDestroy {

  @ViewChild("verManillaModal") VerManillaModal: VerManillaAdminComponent;

  private _unsubscribeAll: Subject<any> = new Subject<any>();

  manillasCount: number = 0;
  manillasList: any = [];
  page: any = 1;
  limit: any = environment.pagination_limit;
  estados: any = [];
  types: any = [];

  allManillas:any = {};

  Toast:any;
  searchInputControl: FormControl = new FormControl('produccion');

  seleccionadas: any = [];

  selectAll: any = [];

  selectInputControl: FormControl = new FormControl(false);

  loading: boolean = false;

  constructor(
    private _userService: UserService,
    private _changeDetectorRef: ChangeDetectorRef,
    private modalService: NgbModal,
    private _formBuilder: FormBuilder,
    private viewScroller: ViewportScroller,
    private _adminService: AdminService
  ) {
    this.Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 3000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    }); 
  }

  ngOnInit(): void {

    this._adminService.manillasProduccion$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((manillas: any) => {
        this.allManillas = manillas;
        // Update the selected agente
        if(this.searchInputControl.value === 'produccion' || this.searchInputControl.value === ''){
          this.manillasCount = manillas.totalDocuments;
          this.manillasList = manillas.manillas;
        }else{
          this.manillasCount = manillas.totalDocumentsResagadas;
          this.manillasList = manillas.manillasResagadas;
        }

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
  }

  /**
     * On destroy
     */
  ngOnDestroy(): void
  {
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
  }

  abrirModalVerManilla(manilla: any){
    this._adminService.updateManillaSelected(manilla);
    this.VerManillaModal.openModal();
  }

  onPageChange(event:any){
    this.getManillasProduccionPaginated(((event - 1) * this.limit));
  }

  getManillasProduccionPaginated(page:any){
    this._adminService.getManillasProductionUpdate(page, this.limit).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this._adminService.updateManillasProductionList(response);
      },(error) => {
        this.Toast.fire({
          icon: 'error',
          title: error.error.message
        });
      }
    );
  }

  enviarManilla(manilla:any){
    this.cambiarEstadoManillaSeleccionada(manilla._id, 'Enviada');
  }
  
  manillaEntregada(manilla:any){
    this.cambiarEstadoManillaSeleccionada(manilla._id, 'Entregada');
  }

  cambiarEstadoManillaSeleccionada(id:any, estado:any){
    this._adminService.cambiarEstadoManilla(id, estado).pipe(takeUntil(this._unsubscribeAll)).subscribe(
      (response:any) => {
        this.Toast.fire({
          icon: "success",
          title: `El estado de la manilla ha sido actualizado con exito a: ${estado}`,
        });
        this.page = 1;
        this.getManillasProduccionPaginated(0);
      },(error) => {
        this.Toast.fire({
          icon: "error",
          title: error.error.message,
        });
      }
    );
  }

  updateManillasList() {
    this._adminService
      .getManillasSolicitudesUpdate(0, this.limit)
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe(
        (response: any) => {
          this._adminService.updateManillasProductionList(response);
        },
        (error) => {
          this.Toast.fire({
            icon: "error",
            title: error.error.message,
          });
        }
      );
  }

  handleChange(event){
    this.seleccionadas = [];
    this.selectInputControl.setValue(false);
    if(event.target.value === 'produccion' || event.target.value === ''){
      this.manillasCount = this.allManillas.totalDocuments;
      this.manillasList = this.allManillas.manillas;
    }else{
      this.manillasCount = this.allManillas.totalDocumentsResagadas;
      this.manillasList = this.allManillas.manillasResagadas;
    }
  }

  onSelect(manilla: any, index: any) {
    if (!manilla.selected) {
      this.seleccionadas.push(manilla._id);
    } else {
      const isInArray = (element) => element === manilla._id;
      let ind = this.seleccionadas.findIndex(isInArray);
      this.seleccionadas.splice(ind, 1);
    }
    // this.seleccionadas.push(manilla._id);
  }

  selectAllManillas() {
    this.seleccionadas = [];
    this.manillasList.forEach((manilla) => {
      if (!this.selectInputControl.value) {
        this.seleccionadas.push(manilla._id);
        manilla.selected = true;
        // manilla.selected = !manilla.selected;
      } else {
        manilla.selected = false;
      }
    });
  }

  aceptarSeleccionadas(){
    if (this.seleccionadas.length !== 0) {
      this.loading = true;
      this._adminService
        .updateEstadoManillaEnviadaVarias({ ids: this.seleccionadas })
        .pipe(takeUntil(this._unsubscribeAll))
        .subscribe(
          (response: any) => {
            this.Toast.fire({
              icon: "success",
              title:
                "Todas las pulseras seleccionadas han sido aceptadas y puestas en produccion.",
            });
            this.updateManillasList();
            this.selectInputControl.setValue(false);
            this.seleccionadas = [];
            this.loading = false;
          },
          (error) => {
            this.Toast.fire({
              icon: "error",
              title: error.error.message,
            });
            this.loading = false;
          }
        );
    } else {
      this.Toast.fire({
        icon: "error",
        title: "Debes seleccionar pulseras para aceptarlas.",
      });
    }
  }

  
}
