import { isPlatformBrowser } from "@angular/common";
import { ChangeDetectorRef, Component, Inject, Input, OnDestroy, OnInit, PLATFORM_ID, TemplateRef, ViewChild } from "@angular/core";
import { AbstractControl, FormBuilder, FormGroup, NgForm, ValidationErrors, ValidatorFn, Validators } from "@angular/forms";
import { Router } from "@angular/router";
import { ModalDismissReasons, NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { Gallery, GalleryItem, ImageItem, ImageSize, ThumbnailsPosition } from "ng-gallery";
import { Lightbox } from "ng-gallery/lightbox";
import { Subject, takeUntil } from "rxjs";
import { AdminService } from "src/app/admin/admin.service";
import { UserService } from "src/app/core/user/user.service";
import { environment } from "src/environments/environment";
import Swal from "sweetalert2";

@Component({
  selector: "app-ver-manilla-admin",
  templateUrl: "./ver-manilla.component.html",
  styleUrls: ["./ver-manilla.component.scss"],
})
export class VerManillaAdminComponent implements OnInit, OnDestroy {

    @Input() tipo: any;
    @ViewChild("modalManilla", { static: false }) ModalManilla: TemplateRef<any>;
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    public closeResult: string;
    public modalOpen: boolean = false;

    manilla: any = {};

    limit: any = environment.pagination_limit;

    Toast:any;

    loading: boolean = true;

    acepting: boolean = true;

    items: GalleryItem[];

    showIcons: any = {};

    fieldsList: any = [];
    constructor(
        @Inject(PLATFORM_ID) private platformId: Object,
        private router: Router, private modalService: NgbModal,
        private _formBuilder: FormBuilder,
        private _userService: UserService,
        private _changeDetectorRef: ChangeDetectorRef,
        private _adminService: AdminService, public gallery: Gallery, public lightbox: Lightbox)
    {
      this.Toast = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer)
          toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
      });  
    }

    ngOnInit(): void {
      this._adminService.manillaSelected$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe((manilla: any) => {
        this.loading = true;
        if(this.tipo === 'solicitudes'){
          this.acepting = true;
        }
        // Update the manilla list
        if(manilla !== null){
          this.getManillaById(manilla.numid);
        }
        // console.log(this.manilla);

        // Mark for check
        this._changeDetectorRef.markForCheck();
      });
    }

    ngOnDestroy() {
      if(this.modalOpen){
        this.modalService.dismissAll();
      }
      // Unsubscribe from all subscriptions
      this._unsubscribeAll.next(null);
      this._unsubscribeAll.complete();
    }

    openModal() {
        this.modalOpen = true;
        if (isPlatformBrowser(this.platformId)) { // For SSR 
          this.modalService.open(this.ModalManilla, { 
            size: 'lg',
            ariaLabelledBy: 'modal-basic-title',
            centered: true,
            windowClass: 'ModalManilla',
            backdrop: 'static',
            keyboard: false,
            scrollable: true
          }).result.then((result) => {
            `Result ${result}`
          }, (reason) => {
            this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
          });
        }
    }

    private getDismissReason(reason: any): string {
        if (reason === ModalDismissReasons.ESC) {
          return 'by pressing ESC';
        } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
          return 'by clicking on a backdrop';
        } else {
          return `with: ${reason}`;
        }
    }

    getManillaById(id:any){
      this._adminService.getManillaById(id).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response:any) => {
          if(response.foto_portador){
            this.items = [new ImageItem({ src: response.foto_portador, thumb: response.foto_portador })];
            // this.items = [new ImageItem({ src: this.imagenTemp, thumb: this.imagenTemp })];
            const lightboxRef = this.gallery.ref('lightbox');
            // Add custom gallery config to the lightbox (optional)
            lightboxRef.setConfig({
              imageSize: ImageSize.Cover,
              thumbPosition: ThumbnailsPosition.Top
            });

            // Load items into the lightbox gallery ref
            lightboxRef.load(this.items);
          }
          
          this.manilla = response;
          this.getFieldsByType(response.tipo);
        },(error) => {
          this.Toast.fire({
            icon: 'error',
            title: error.error.message
          });
        }
      );
    }

    getFieldsByType(type: any): void {
      this._userService.getFieldsByUserTypes(type).pipe(takeUntil(this._unsubscribeAll)).subscribe(
          (response: any) => {
            this.fieldsList = response;
            this.loading = false;
          },
          (error) => {
            console.log(error);
          }
        );
    }

    aceptarManillasById(){
      if(this.acepting){
        this.acepting = false;
        this._adminService.aceptarManillaById(this.manilla._id).pipe(takeUntil(this._unsubscribeAll)).subscribe(
          (response:any) => {
            this.Toast.fire({
              icon: 'success',
              title: 'La manilla ha sido aceptada y puesta en proceso de produccion'
            });
            this.updateManillasList();
          },(error) => {
            this.acepting = true;
            this.Toast.fire({
              icon: 'error',
              title: error.error.message
            });
          }
        );
      }
    }

    rechazarManillasById(){
      if(this.acepting){
        this.acepting = false;
        this._adminService.rechazarManillaById(this.manilla._id).pipe(takeUntil(this._unsubscribeAll)).subscribe(
          (response:any) => {
            this.Toast.fire({
              icon: 'success',
              title: 'La manilla ha sido rechazada con exito'
            });
            this.updateManillasList();
          },(error) => {
            this.acepting = true;
            this.Toast.fire({
              icon: 'error',
              title: error.error.message
            });
          }
        );
      }
    }

    updateManillasList(){
      this._adminService.getManillasSolicitudesUpdate(0, this.limit).pipe(takeUntil(this._unsubscribeAll)).subscribe(
        (response:any) => {
          this._adminService.updateManillasSolicitudesList(response);
          this.modalService.dismissAll();
        },(error) => {
          this.Toast.fire({
            icon: 'error',
            title: error.error.message
          });
        }
      );
    }

    submit(): void{
        
    }

    cambiarEstado(index:any){
      if(this.showIcons['campo'+index]){
        console.log("object");
        this.showIcons['campo'+index] = false;
      }else{
        console.log("2");
        this.showIcons['campo'+index] = true;
      }

      this._changeDetectorRef.markForCheck();

    }

    cambiarEstadoEntregadaManilla() {
      this.acepting = false;
      this._adminService.updateEstadoManillaEntregada(this.manilla._id).pipe(takeUntil(this._unsubscribeAll)).subscribe(
          (response: any) => {
            this.Toast.fire({
              icon: "success",
              title: "La pulsera ha sido actualizada a entregada.",
            });
            this.getManillasEnviadasPaginated(0);
            this.acepting = true;
            this._changeDetectorRef.markForCheck();
          },
          (error) => {
            this.acepting = true;
            this.Toast.fire({
              icon: "error",
              title: error.error.message,
            });
            this._changeDetectorRef.markForCheck();
          }
        );
    }

    getManillasEnviadasPaginated(page: any) {
      this._adminService
        .getManillasEnviadasUpdate(page, this.limit)
        .pipe(takeUntil(this._unsubscribeAll))
        .subscribe(
          (response: any) => {
            this._adminService.updateManillasEnviadasList(response);
            this.modalService.dismissAll();
          },
          (error) => {
            this.Toast.fire({
              icon: "error",
              title: error.error.message,
            });
          }
        );
    }
}
